/*General rules*/

* {
    outline: 0px solid red; /*comment this out before publishing!!*/
}

a {
    color:black;
    text-decoration: underline;
    text-decoration-color: whitesmoke;
    transition: text-decoration-color 0.2s;
}

a:hover {
    text-decoration-color: black;
    transition: text-decoration-color 0.3s;
}

body {
    margin: 0;
    width: 100vw;
    height: 100vh;/*not sure why this needs to be here*/
}
h1, h2, h3, h4, h5, h6 {
    font-family: 'Libre Bodoni', serif;
    font-weight: normal;
}
p, li {
    font-family: 'Mulish', sans-serif;
    font-size: 18px;
}

/*main*/
/*try to keep this section to rules that could apply to ALL possible html snippets*/
.header-shadow {
    width: 100%;
    height: 1em;
    position: fixed;
    top: 5em;
    z-index:1;
    background-image: -webkit-linear-gradient(270deg,rgba(49,49,49,0.88) 1.45%,rgba(49,49,49,0.34) 21.14%,rgba(255,255,255,0.00) 44.97%);
    background-image: -moz-linear-gradient(270deg,rgba(49,49,49,0.88) 1.45%,rgba(49,49,49,0.34) 21.14%,rgba(255,255,255,0.00) 44.97%);
    background-image: -o-linear-gradient(270deg,rgba(49,49,49,0.88) 1.45%,rgba(49,49,49,0.34) 21.14%,rgba(255,255,255,0.00) 44.97%);
    background-image: linear-gradient(180deg,rgba(49,49,49,0.88) 1.45%,rgba(49,49,49,0.34) 21.14%,rgba(255,255,255,0.00) 44.97%);
    visibility: show;
}

.splash {
    position: relative;
    width: 100%;
    height: 5em;
    top: 5em;
    background-color:whitesmoke;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index:3;
}
main {
    width: 100%;
    position: relative;
    top: 3.8em;
    min-height: 81.2vh;
    height: 100vh;
    margin-top: 0;
    margin-bottom: 0;

}
main h3, main p {
    color: whitesmoke;
    text-shadow: 0 0.015em 10px #101010,0 -0.015em #101010,0.01em 0 #101010,-0.01em 0 #101010;
}
.text-container {
    /* position: relative; */
    display: flex;
    height: 100%;
    flex-direction: column;
    /* align-items: flex-start; */
    justify-content: center;
    margin-left: 3em;
    margin-right: 3em;
    min-height: 60vh;
    max-height: 60vh;
    /* align-items: flex-end; */
    justify-content: center;
    /* margin-left: auto;
    margin-right: 5em; */
    /* text-align:right; */
    max-width: 50%;
    min-width: 40em;
}
.cover {
    min-height: 100%;
    display: flex;
    /* align-items: flex-end; */
}
.text-container h3 {
    font-size: 4em;
}

.text-container p {
    font-size: 2em;
}

.intro {
    height: 100%;
    /* position: relative;
    top: 3.8em; */
    background-color: whitesmoke;
    display: flex;
    flex-direction: column;
}
.intro .text-container h4 {
    font-size: 2.5em;
}
.intro .text-container p {
    color: black;
    font-size: 1.3em;
}

.first {
    align-self: flex-end;
    text-align: end;
}

.second {
    align-self: flex-start;
}

/*Mobile Formatting*/
.mobile-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    align-content: center;
}
.mobile-head h1 {
    margin-left: 1vh;
}
.mobile-head p {
    margin-right: 1vh;
}

.mobile-home {
    position: relative;
    top: 10vh;
    height: 80 vh;
}

.mobile-home-image {
    background-image: url("images/Home.png");
    height: 40vh;
    background-size:cover;
}

.mobile-foot {
    position: relative;
    top: 6vh;
    display: flex;
    justify-content: space-between;
}
.mobile-footer-left {
    margin-left: 1vh;
}
.mobile-footer-right {
    margin-right: 1vh;
    text-align: right;
}